<template>
  <div class="createPost-container">
    <el-form class="form-container" :model="postForm" :rules="rules" ref="postForm">

      <sticky :className="'sub-navbar '+postForm.status">
        <template v-if="fetchSuccess">

          <router-link style="margin-right:15px;" v-show='isEdit' :to="{ path:'create-form'}">
            <el-button type="info">form</el-button>
          </router-link>

          <el-dropdown trigger="click">
            <el-button plain>{{!postForm.comment_disabled?'':''}}
              <i class="el-icon-caret-bottom el-icon--right"></i>
            </el-button>
            <el-dropdown-menu class="no-padding" slot="dropdown">
              <el-dropdown-item>
                <el-radio-group style="padding: 10px;" v-model="postForm.comment_disabled">
                  <el-radio :label="true"></el-radio>
                  <el-radio :label="false"></el-radio>
                </el-radio-group>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>

          <el-dropdown trigger="click">
            <el-button plain>
              <i class="el-icon-caret-bottom el-icon--right"></i>
            </el-button>
            <el-dropdown-menu class="no-border" slot="dropdown">
              <el-checkbox-group v-model="postForm.platforms" style="padding: 5px 15px;">
                <el-checkbox v-for="item in platformsOptions" :label="item.key" :key="item.key">
                  {{item.name}}
                </el-checkbox>
              </el-checkbox-group>
            </el-dropdown-menu>
          </el-dropdown>

          <el-dropdown trigger="click">
            <el-button plain>
              
              <i class="el-icon-caret-bottom el-icon--right"></i>
            </el-button>
            <el-dropdown-menu class="no-padding no-border" style="width:300px" slot="dropdown">
              <el-form-item label-width="0px" style="margin-bottom: 0px" prop="source_uri">
                <el-input placeholder="" v-model="postForm.source_uri">
                  <template slot="prepend">url</template>
                </el-input>
              </el-form-item>
            </el-dropdown-menu>
          </el-dropdown>

          <el-button v-loading="loading" style="margin-left: 10px;" type="success" @click="submitForm()">
          </el-button>
          <el-button v-loading="loading" type="warning" @click="draftForm"></el-button>

        </template>
        <template v-else>
          <el-tag>,,</el-tag>
        </template>

      </sticky>

      <div class="createPost-main-container">
        <el-row>
          <el-col :span="21">
            <el-form-item style="margin-bottom: 40px;" prop="title">
              <!-- <MDinput name="name" v-model="postForm.title" required :maxlength="100">
                
              </MDinput> -->
              <span v-show="postForm.title.length>=26" class='title-prompt'>app</span>
            </el-form-item>

            <div class="postInfo-container">
              <el-row>
                <el-col :span="8">
                  <el-form-item label-width="45px" label=":" class="postInfo-container-item">
                    <multiselect v-model="postForm.author" :options="userLIstOptions" @search-change="getRemoteUserList" placeholder="" selectLabel=""
                      deselectLabel="" track-by="key" :internalSearch="false" label="key">
                      <span slot='noResult'></span>
                    </multiselect>
                  </el-form-item>
                </el-col>

                <el-col :span="8">
                  <el-tooltip class="item" effect="dark" content="" placement="top">
                    <el-form-item label-width="50px" label=":" class="postInfo-container-item">
                      <el-input placeholder="" style='min-width:150px;' v-model="postForm.source_name">
                      </el-input>
                    </el-form-item>
                  </el-tooltip>
                </el-col>

                <el-col :span="8">
                  <el-form-item label-width="80px" label=":" class="postInfo-container-item">
                    <el-date-picker v-model="postForm.display_time" type="datetime" format="yyyy-MM-dd HH:mm:ss" placeholder="">
                    </el-date-picker>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </el-col>
        </el-row>

        <el-form-item style="margin-bottom: 40px;" label-width="45px" label=":">
          <el-input type="textarea" class="article-textarea" :rows="1" autosize placeholder="" v-model="postForm.content_short">
          </el-input>
          <span class="word-counter" v-show="contentShortLength">{{contentShortLength}}</span>
        </el-form-item>

        <div class="editor-container">
          <!-- <tinymce :height=400 ref="editor" v-model="postForm.content"></tinymce> -->
        </div>

        <div style="margin-bottom: 20px;">
          <Upload v-model="postForm.image_uri"></Upload>
        </div>
      </div>
    </el-form>

  </div>
</template>

<script>
// import Tinymce from '@/components/Tinymce'
import Upload from '@/components/Upload/singleImage3';
// import MDinput from '@/components/MDinput'
// element-uiselect
import Multiselect from 'vue-multiselect';
// css
import 'vue-multiselect/dist/vue-multiselect.min.css';
// header
import Sticky from '@/components/Sticky';
import { validateURL } from '@/utils/validate';
import { fetchArticle } from '@/api/old/article';
import { userSearch } from '@/api/old/remoteSearch';

const defaultForm = {
  status: 'draft',
  title: '', // 
  content: '', // 
  content_short: '', // 
  source_uri: '', // 
  image_uri: '', // 
  source_name: '', // 
  display_time: undefined, // 
  id: undefined,
  platforms: ['a-platform'],
  comment_disabled: false,
};

export default {
  name: 'articleDetail',
  components: {
    // Tinymce,
    // MDinput,
    Upload,
    Multiselect,
    Sticky,
  },
  props: {
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    const validateRequire = (rule, value, callback) => {
      if (value === '') {
        this.$message({
          message: rule.field + '',
          type: 'error',
        });
        callback(null);
      } else {
        callback();
      }
    };
    const validateSourceUri = (rule, value, callback) => {
      if (value) {
        if (validateURL(value)) {
          callback();
        } else {
          this.$message({
            message: 'url',
            type: 'error',
          });
          callback(null);
        }
      } else {
        callback();
      }
    };
    return {
      postForm: Object.assign({}, defaultForm),
      fetchSuccess: true,
      loading: false,
      userLIstOptions: [],
      platformsOptions: [
        { key: 'a-platform', name: 'a-platform' },
        { key: 'b-platform', name: 'b-platform' },
        { key: 'c-platform', name: 'c-platform' },
      ],
      rules: {
        image_uri: [{ validator: validateRequire }],
        title: [{ validator: validateRequire }],
        content: [{ validator: validateRequire }],
        source_uri: [{ validator: validateSourceUri, trigger: 'blur' }],
      },
    };
  },
  computed: {
    contentShortLength() {
      return this.postForm.content_short.length;
    },
  },
  created() {
    if (this.isEdit) {
      this.fetchData();
    } else {
      this.postForm = Object.assign({}, defaultForm);
    }
  },
  methods: {
    fetchData() {
      fetchArticle()
        .then(res => {
          this.postForm = res.data;
        })
        .catch(err => {
          this.fetchSuccess = false;
          console.log(err);
        });
    },
    submitForm() {
      this.postForm.display_time = parseInt(this.display_time / 1000, 10);
      console.log(this.postForm);
      this.$refs.postForm.validate(valid => {
        if (valid) {
          this.loading = true;
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000,
          });
          this.postForm.status = 'published';
          this.loading = false;
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    draftForm() {
      if (
        this.postForm.content.length === 0 ||
        this.postForm.title.length === 0
      ) {
        this.$message({
          message: '',
          type: 'warning',
        });
        return;
      }
      this.$message({
        message: '',
        type: 'success',
        showClose: true,
        duration: 1000,
      });
      this.postForm.status = 'draft';
    },
    getRemoteUserList(query) {
      userSearch(query).then(res => {
        if (!res.data.items) return;
        console.log(res);
        this.userLIstOptions = res.data.items.map(v => ({
          key: v.name,
        }));
      });
    },
  },
};
</script>

